.product-list-container {
    text-align: center;
    padding: 20px;
  }
  
  .product-header {
    font-size: 28px;
    font-weight: bold;
    margin-bottom: 15px;
  }
  
  /* Controls wrapper to hold filters and sorting in one horizontal line */
  .controls-wrapper {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: nowrap;
    width: 100%;
    margin-bottom: 20px;
    padding: 10px 20px;
    background: rgba(255, 255, 255, 0.1); /* Light transparency */
    backdrop-filter: blur(10px); /* Glassmorphism effect */
    border-radius: 8px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
    z-index: 10;
  }
  
  
  /* Filters group on the left */
  .filters-group {
    display: flex;
    gap: 10px;
    flex-wrap: nowrap;
  }
  /* Search bar container */
.search-container {
    position: relative; /* Ensure relative positioning for absolute child */
    width: 250px;
  }
  
  /* Search input box */
  .search-bar {
    width: 100%;
    padding: 8px 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 14px;
  }
  
  /* Suggestions dropdown */
  .suggestions-list {
    position: absolute;
    top: 100%; /* Places it directly below the search bar */
    left: 0;
    width: 100%;
    background: white;
    color: #5e7276;
    border: 1px solid #ddd;
    border-radius: 5px;
    list-style: none;
    padding: 5px 0;
    margin-top: 5px; /* Prevents overlap with input */
    z-index: 1000; /* Keeps it above product images */
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
    max-height: 200px;
    overflow-y: auto;
  }
  
  /* Ensures each item is styled correctly */
  .suggestion-item {
    padding: 10px;
    cursor: pointer;
    font-size: 14px;
    background: white;
  }
  
  .suggestion-item:hover {
    background: #f0f0f0;
  }
  /* Sorting group on the right */
  .sorting-group {
    display: flex;
    gap: 10px;
    flex-wrap: nowrap;
  }
  
  /* Buttons styling */
  .filter-btn,
  .sort-btn {
    background: #283048;
    color: white;
    padding: 8px 12px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background 0.3s ease;
  }
  
  .filter-btn.active {
    background: #859398;
  }
  
  .filter-btn:hover,
  .sort-btn:hover {
    background: #5e7276;
  }
  
  /* Product Grid */
  .product-grid {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;
    position: relative; /* Ensures it does not interfere */
    z-index: 1;
  }
  